<template>
  <div id='main'>
    <div id='orders'>
      <div id='order-header'>
        <p>历史订单</p>
      </div>
      <div id='order-menu'>
        <div v-for='(item, index) in orderList' class='menu-item' :class="{'delete': delCheck(index)}">
          <div class='item-content' @click='$root.learnFood(2, item)'>
              <div class='fd-pic' :style="$root.renderFoodPic(item.photo)"></div>
              <div class='fd-intro'>
                <p>{{item.date.split('T')[0]}}</p>
                <p class="single-wrap">{{item.name}}</p>
                <p>{{item.restaurant}}食堂</p>
                <p>￥{{item.price}}</p>
              </div>
          </div>
          <div class='commentBox' @click='comment(index)'>
            <img src='static/assets/comment.png' alt='buy'>
          </div>
          <div class='oneMoreBox' @click='oneMore(index)'>
            <img src='static/assets/onemore.png' alt='oneMore'>
          </div>
        </div>
      </div>
    </div>
    <transition name="dialog">
      <div v-show='showDialog' id='dialog-box' :class="{'init-box': showDialog}">
        <div id='box-content'>
          <div id='close' @click='quit0'></div>
          <form v-show="!isSuccess && dialogMode === 2" id='form' v-on:submit.prevent='insert'>
            <p id='remark-name'>{{feedback.name}}</p>
            <p class='grade-name'>评价商品</p>
            <p class='mark'></p>
            <input id='food' class='grade' type='range' name='food' step='20'>
            <p class='grade-name'>评价服务</p>
            <p class='mark'></p>
            <input id='service' class='grade' type='range' name='service' step='20'>
            <textarea id='comment' class='comment' placeholder='说说哪里满意或不满意，帮大家选择，帮食堂改进'></textarea>
            <input class='submit' type='submit'>
          </form>
          <form v-show="dialogMode === 1" id='form2'>
            <div id='form2-content' @click='learnFood'>
              <img id='buy-box-pic' src='static/assets/food.jpeg' alt='food'>
              <p id='food-name'>{{buy.name}}</p>
              <p>月售357份</p>
              <p>好评率96%</p>
              <p>￥8.9</p>
            </div>
            <div id='buyBox' @click="beginBuy"></div>
          </form>
          <img v-show="isSuccess" class="success" src="static/assets/success.png" alt="success">
          <transition name="prompt"><p v-show="isSuccess" id="prompt">评论成功</p></transition>
          <transition name="check"><p v-show="isSuccess" id="click-to-check" @click="learnFood">点击此处看你的评论</p></transition>
        </div>
      </div>
    </transition>
    <transition name="grey">
      <div v-show='showDialog' class='grey' @click.stop='quit0'></div>
    </transition>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'orders',
    data () {
      return {
        index2: -1,
        dialogMode: 0,
        remarkResult: false,
        showDialog: false,
        isSuccess: false,
        buy: {
          name: ''
        },
        feedback: {
          name: ''
        },
        dialogStatus: false
      }
    },
    methods: {
      comment: function (temp) {
        this.dialogMode = 2
        this.feedback.name = this.orders[temp].name
        this.remark(temp)
      },
      oneMore: function (temp) {
        this.dialogMode = 1
        this.buy.name = this.orders[temp].name
        this.cartShow()
      },
      remark: function (id) {
        if (!this.dialogStatus) {
          this.dialogStatus = true
          console.log(this.dialogStatus)
          this.index2 = id
          this.dialogModeSet()
          this.reset()
          $('.grade').on('input', function () {
            var value = Number($(this).val())
            var value2 = value + 1
            $(this).css('background', 'linear-gradient(to right, #059CFA, #059CFA ' + value + '%, white ' + value2 + '%, white 100%)')
            if ($(this).attr('name') === 'food') {
              $('.mark').eq(0).text(($(this).val() / 20).toFixed(1))
            } else {
              $('.mark').eq(1).text(($(this).val() / 20).toFixed(1))
            }
          })// 此处应改进
          this.showDialog = true
        } else {
          console.log('too frequent')
        }
      },
      reset: function () {
        $('.mark').text('4.0')
        $('#form textarea').val('')
        $('.grade').val(80)
        $('.grade').css('background', 'linear-gradient(to right, #059CFA, #059CFA ' + 80 + '%, white ' + 81 + '%, white 100%)')
      },
      quit0: function () {
        this.showDialog = false
        if (this.remarkResult && this.dialogMode === 2) {
          this.isSuccess = false
          this.deleteItem()
        } else {
          this.dialogStatus = false
          // console.log(this.dialogStatus)
        }
      },
      deleteItem: function () {
        var self = this
        setTimeout(function () {
          $('.menu-item:gt(' + self.index2 + ')').addClass('wrap-item')
          setTimeout(function () {
            $('.menu-item:gt(' + self.index2 + ')').removeClass('wrap-item')
            self.orders.splice(self.index2, 1)
            self.remarkResult = false
            self.dialogStatus = false
            // console.log(self.dialogStatus)
            self.index2 = -1
          }, 400)
        }, 600)
      },
      dialogModeSet: function () {
        if (this.dialogMode === 1) {
          $('#close').css('background', 'url(\'static/assets/close.png\') 56% 50% no-repeat')
        } else {
          $('#close').css('background', 'url(\'static/assets/close2.png\') 56% 50% no-repeat')
        }
        $('#close').css('background-size', '44%')
      },
      cartShow: function () {
        if (!this.dialogStatus) {
          this.dialogStatus = true
          this.dialogModeSet()
          this.showDialog = true
        } else {
          // console.log('too frequent')
        }
      },
      learnFood: function (data) {
        this.$root.previous = 2
        this.$store.commit('setFoodInfo', data)
        this.$router.push('/food')
      },
      insert: function () {
        var self = this
        var food = $('#food').val()
        var service = $('#service').val()
        var comment = $('#comment').val()
        // eslint-disable-next-line
        $.post('/insert_comment/', {'food': food, 'service': service, 'comment': comment, csrfmiddlewaretoken: csrf_token}, function (data) {
          self.reset()
          self.isSuccess = true
          self.remarkResult = true
          console.log(data)
          setTimeout(function () {
            if (self.showDialog) {
              self.quit0()
            }
          }, 3800)
        })
      },
      delCheck: function (i) {
        if (i === this.index2 && this.remarkResult && !this.showDialog) {
          return true
        } else {
          return false
        }
      },
      beginBuy: function () {
        alert('buy')
      }
    },
    computed: {
      orderList: function () {
        return this.$store.state.orderList
      }
    },
    mounted () {
      let self = this
      $('#orders').scrollTop(this.$root.y2)
      $('#orders').scroll(function () {
        self.$root.y2 = $(this).scrollTop()
      })
      $.ajax({
        async: false,
        url: '/order/',
        type: 'POST',
        data: {
          user_id: 2,
          // eslint-disable-next-line no-undef
          csrfmiddlewaretoken: csrf_token
        },
        success: function (data) {
          self.$store.commit('setOrderList', data)
          console.log(data)
        },
        error: function () {
          console.log('获取历史订单失败！')
        }
      })
    }
  }
</script>

